import React, { useState, useLayoutEffect, useEffect } from 'react';
import { Button } from 'reactstrap';
import axios from 'axios';

import App, { itTQue } from '../../Questions';
import Header from '../../Header';
import Footer from '../../Footer';

const questions = itTQue;
const token = 'YWRtaW5hZG1pbjEyMw==';

export default function ItTQue(props) {
    useLayoutEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
      if(Log != true){
        props.history.push("/");
      }
    }, []);
    
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const [showScore, setShowScore] = useState(false);
    const [score, setScore] = useState(0);
    const [correct, setCorrect] = useState('0');
    const [values, setValues] = useState('');

    let User = sessionStorage.getItem('user')
    User = JSON.parse(User)
    let Std = sessionStorage.getItem('standard')
    Std = JSON.parse(Std)

window.onload = function() {
        if(!window.location.hash) {
            window.location = window.location + '#loaded';
            window.location.reload();
        }
    }

    useEffect(() => {
        setValues({
            u_id: User.u_id, standard: Std, field: 'Degree', stream: 'IT', rightanswer: score,
        })
  }, [score])

    const handleAnswerOptionClick = (isCorrect) => {
        if (isCorrect) {
            setScore(score + 1);
        }

        const nextQuestion = currentQuestion + 1;
        if (nextQuestion < questions.length) {
            setCurrentQuestion(nextQuestion);
        } else {
            setShowScore(true);
        }
    };

    const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },
    };

    const Test = () => {
        axios.post(
          'https://globalarey.in/career_guidance/api/studentexams/',
          values,
          config,
        ).then((response)=> {
          console.log(response.data.response);

          if(response.data.response.message == 'exam score added.') {
            console.log('true');
            props.history.push("/Profile");
          }
          else {
            console.log('false');
          }

        }).catch();
        console.log(values);
      };

    return (
        <div>
            <Header />

        <div className='rd-que'>
        <div className='app'>
            {showScore ? (
                <div className='score-section column'>
                    <div>
                        You scored {score} out of {questions.length}
                    </div>
                    <div className='text-center'>
                      <Button className='my-4' color='primary' type='button' onClick={Test} >
                        Done
                      </Button>
                    </div>
                </div>
            ) : (
                <>
                    <div className='question-section'>
                        <div className='question-count'>
                            <span>Question {currentQuestion + 1}</span>/{questions.length}
                        </div>
                        <div className='question-text'>{questions[currentQuestion].questionText}</div>
                    </div>
                    <div className='answer-section'>
                        {questions[currentQuestion].answerOptions.map((answerOption) => (
                            <button onClick={() => handleAnswerOptionClick(answerOption.isCorrect)}>{answerOption.answerText}</button>
                        ))}
                    </div>
                </>
            )}
        </div>
        </div>
        <Footer />
        </div>
    );
}
