import React, { useLayoutEffect, useState, useCallback } from 'react';
import axios from 'axios';
import { Button } from 'reactstrap';
import history from '../history.js';

import Header from '../components/Header';
import Blog from '../components/Blog';
import Footer from '../components/Footer';

const token = 'YWRtaW5hZG1pbjEyMw==';

function Main(props) {
  const [port, setPort] = useState([]);

  useLayoutEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
      if(Log != true){
        props.history.push("/");
      } else {
        getData();
      }
    }, []);

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value"
  };


  const getData = () =>{
  axios.post(
    'https://globalarey.in/career_guidance/api/blogs/read.php',
    bodyParameters,
    config
  ).then((response)=> {
    setPort(response.data.response.blogs)
    console.log(response.data.response.blogs)
    console.log('hi');
  }).catch();
}

  return (
    <div className="App">
      <Header />

      <Blog port={port}></Blog>
      <div className='center'>
      <Button className='review' color='warning' type='button' onClick={() => props.history.push('/NewBlog')} >
        Add Your Review 
      </Button>
      </div>
      <Footer />
    </div>
  );
}

export default Main;
