import React, { Component } from 'react';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import reportWebVitals from './reportWebVitals';
import history from './history'

import './assets/css/style.css';
import './assets/css/bootstrap.min.css';
import './assets/css/agency.min.css';
import './assets/css/landing-page.css';
import './assets/css/home.css';
import './assets/css/maps/home.css.map';
import './assets/css/maps/landing-page.css.map';
import './assets/css/maps/style.css.map';
import './assets/css1/style.css';
import './assets/css1/bootstrap.min.css';

import Login from './Pages/Login';
import Register from './Pages/Register';
import EduDetails from './Pages/EduDetails';
import Main from './Pages/Main';
import Contact from './Pages/Contact';
import About from './Pages/About';
import Blog from './Pages/Blog';
import Profile from './Pages/Profile';
import NewBlog from './Pages/NewBlog';
import Standard from './Pages/Standard';

import Twelve from './Pages/Fields/Twelve';
import Diploma from './Pages/Fields/Diploma';
import Degree from './Pages/Fields/Degree';

import ArtsQue from './components/Test/Twelve/ArtsQue';
import ComQue from './components/Test/Twelve/ComQue';
import SciQue from './components/Test/Twelve/SciQue';
import CompQue from './components/Test/Diploma/CompQue';
import ItQue from './components/Test/Diploma/ItQue';
import EleQue from './components/Test/Diploma/EleQue';
import CompTQue from './components/Test/Degree/CompTQue';
import ItTQue from './components/Test/Degree/ItTQue';
import EleTQue from './components/Test/Degree/EleTQue';

export default class App extends Component {
  state = {
    user: null,
  };
  render(){
    return (
    <div>
      <Router history={history}>

          <Switch>
          <Route exact path="/" component={Login} />
          <Route path="/Register" component={Register} />
          <Route path="/EduDetails" component={EduDetails} />

          <Route path='/Main' component={(props) => <Main user={this.state.user} {...props} />} />
          <Route path="/Contact" component={Contact} />
          <Route path="/About" component={About} />
          <Route path="/Blog" component={Blog} />
          <Route path="/Profile" component={Profile} />
          <Route path="/NewBlog" component={NewBlog} />

          <Route path="/Standard" component={Standard} />
          <Route path="/Twelve" component={Twelve} />
          <Route path="/Diploma" component={Diploma} />
          <Route path="/Degree" component={Degree} />

          <Route path="/ArtsQue" component={ArtsQue} />
          <Route path="/ComQue" component={ComQue} />
          <Route path="/SciQue" component={SciQue} />
          <Route path="/CompQue" component={CompQue} />
          <Route path="/ItQue" component={ItQue} />
          <Route path="/EleQue" component={EleQue} />
          <Route path="/CompTQue" component={CompTQue} />
          <Route path="/ItTQue" component={ItTQue} />
          <Route path="/EleTQue" component={EleTQue} />
          </Switch>

    </Router>
    </div>
  )
}
}
