import React, { useLayoutEffect, useState, useCallback, useEffect } from 'react';
import { Button } from 'reactstrap';

import axios from 'axios';

import Header from '../components/Header';
import About from '../components/About';
import Footer from '../components/Footer';

const token = 'YWRtaW5hZG1pbjEyMw==';

function Standard(props) {
  const [standard, setStandard] = useState([]);
  let Std = sessionStorage.getItem('standard')
  Std = JSON.parse(Std)

  const ten = [
    {
      title: 'Class 11-12',
      descript: 'In India, the equivalent grade is commonly referred to as "Class 12", or "plus 2". It is also called Intermediate College ("Inter"), HSC, Second Year Junior College (SYJC) or Pre-University Course (PUC) in different regions and states. The CBSE and ICSE boards issue course and examination guidelines for Class 10 and 12 nationally.',
      click: '/Twelve',
    },
    {
      title: 'Diploma',
      descript: 'In India, a diploma is a specific academic award usually earned in professional/vocational courses, e.g., Diploma in Engineering, Diploma in Nursing, Diploma in Pharmacy etc. Engineering diploma is concentrated for the area of study, e.g., diploma in Electronics Engineering, Electrical Engineering, Civil Engineering, computer engineering etc.',
      click: '/Diploma',
    }
  ]

  const twelve = [
    {
      title: 'Diploma',
      descript: 'In India, a diploma is a specific academic award usually earned in professional/vocational courses, e.g., Diploma in Engineering, Diploma in Nursing, Diploma in Pharmacy etc. Engineering diploma is concentrated for the area of study, e.g., diploma in Electronics Engineering, Electrical Engineering, Civil Engineering, computer engineering etc.',
      click: '/Diploma',
    },
    {
      title: 'Degree',
      descript: 'In India, the Bachelor of Engineering (BE) is a professional undergraduate degree awarded after completion of four years of engineering study. Many Indian universities offer degree in engineering disciplines under the name of Bachelor of Technology (B.Tech) instead of BE. Institutes of National Importance like (IIT) and (NIT) also use B.Tech nomenclature.',
      click: '/Degree'
    }
  ]

  const diploma = [
    {
      title: 'Class 11-12',
      descript: 'In India, the equivalent grade is commonly referred to as "Class 12", or "plus 2". It is also called Intermediate College ("Inter"), HSC, Second Year Junior College (SYJC) or Pre-University Course (PUC) in different regions and states. The CBSE and ICSE boards issue course and examination guidelines for Class 10 and 12 nationally.',
      click: '/Twelve'
    },
    {
      title: 'Degree',
      descript: 'In India, the Bachelor of Engineering (BE) is a professional undergraduate degree awarded after completion of four years of engineering study. Many Indian universities offer degree in engineering disciplines under the name of Bachelor of Technology (B.Tech) instead of BE. Institutes of National Importance like (IIT) and (NIT) also use B.Tech nomenclature.',
      click: '/Degree'
    }
  ]

  window.onload = function() {
        if(!window.location.hash) {
            window.location = window.location + '#loaded';
            window.location.reload();
        }
    }

  useLayoutEffect(() => {
    let Log = sessionStorage.getItem('log')
    Log = JSON.parse(Log)
    if(Log != true){
      props.history.push("/");
    }
  }, []);

  useEffect(() => {
    setStd();
  }, [])

  const setStd = () => {
    console.log(Std)
    if(Std == '10 TH'){
        setStandard(ten);
      } else if(Std=='12 TH'){
        setStandard(twelve);
      } else {
        setStandard(diploma);
    }
  }

  return (
    <div className="App">
      <Header />

      <section className="page-section stream-sec" id="portfolio">
        <div className="stream">
          <div className="row">
            <div className="col-lg-12 text-center">
              <h2 className="section-heading text-uppercase">Fields</h2>
              <h3 className="section-subheading text-muted">Next fields you can plan for.</h3>
            </div>
          </div>
          <div className="row port-row-std">
            {
                standard && standard.map(Standard =>
                    <div className="col-md-6 col-sm-6 portfolio-item">
                      <div className='portfo port-wdth'>
                        <div className="portfolio-work">
                            <h4 className='port-ttll'>{Standard.title}</h4>
                            <p className="mtd">{Standard.descript}</p>
                        </div>
                        <div className='text-cntr'>
                            <div>
                              <Button color='primary' type='button' onClick={() => props.history.push(Standard.click)} >
                                Select
                              </Button>
                            </div>
                        </div>
                      </div>
                    </div>
                )
            }
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
}

export default Standard;
