import React, { useState, useLayoutEffect } from 'react';
import axios from 'axios';

import history from '../history.js';

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
  Media,
} from 'reactstrap';

const Register = (props) => {

  const [values, setValues] = useState({
    name: '', phone: '', email: '', password: '',
  });

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },
    };

  const Next = () => {

    const reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

      if(values.name.length <4)
      {
        alert('Enter full name');
      }
      else if(values.name ==='')
      {
        alert('name field is empty');
      }
      else
      {
        if(reg.test(values.email) === false)
        {
          alert('Enter email address properly');
        }
        else if(values.email === '')
        {
          alert('Email field is empty..');
        }
        else
        {
          if(values.phone.length >10 || values.phone.length<10)
          {
            alert('Enter mobile number properly..');
          }
          else if(values.phone ==='')
          {
            alert(' Mobile number field is empty..');
          }
          else
          {
            if(values.password.length <7)
            {
              alert('enter password properly');
            }
            else if(values.password ==='')
            {
              alert('password field is empty..');
            }
            else
            {
              if(values.name.length >4 && values.password.length >=7 && reg.test(values.email) === true && values.phone.length===10)
              {
    axios.post(
      'https://globalarey.in/career_guidance/api/user/',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);

      if(response.data.response.message == 'user created.') {
        global.foo = response.data.response.u_id;
        props.history.push("/EduDetails");
      }
      else {
        alert(response.data.response.message)
        console.log('false');
      }

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
    }
           }
         }
       }
     }
   }

  return(
    <div>
    <section className="ftco-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-7 col-lg-5">
              <div className="wrap">
                <div className="img">
                  <img className="img-fluid" src={ `https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Ftse2.mm.bing.net%2Fth%3Fid%3DOIP.iDnBQNVrdYhG1LSPqHtNxgHaDf%26pid%3DApi&f=1` } alt="portfolio_img" />
                </div>
                <div className="login-wrap p-4 p-md-5 mg-btm">
                  <div className="d-flex">
                    <div className="w-100">
                      <h3 className="mb-4">Sign Up</h3>
                    </div>
                  </div>
                  <Form role='form' className="signin-form">
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>

                    <Input className="form-control" placeholder='Username' type='text' autoComplete='new-email' onChange={set('name')} required/>
                  </InputGroup>
                </FormGroup>
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>
                    <Input className="form-control" placeholder='Mobile' type='tel' autoComplete='new-password' onChange={set('phone')} required/>
                  </InputGroup>
                </FormGroup>
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>
                    <Input className="form-control" placeholder='Email' type='email' autoComplete='new-password' onChange={set('email')} required/>
                  </InputGroup>
                </FormGroup>
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>
                    <Input className="form-control" placeholder='Password' type='password' autoComplete='new-password' onChange={set('password')} required/>
                  </InputGroup>
                </FormGroup>
                <div className='text-center'>
                  <Button className='my-4' color='primary' type='button' onClick={Next} >
                    Next
                  </Button>
                </div>
              </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default Register;
