import React, { useLayoutEffect, useState, useCallback } from 'react';
import { Button } from 'reactstrap';

import axios from 'axios';

import Header from '../../components/Header';
import Footer from '../../components/Footer';

const token = 'YWRtaW5hZG1pbjEyMw==';

function Twelve(props) {
  useLayoutEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
      if(Log != true){
        props.history.push("/");
      }
    }, []);

  const fields = [
    {
      title: 'Arts',
      descript: 'Arts stream offers numerous career options and opportunities to students. It is a very traditional stream. This is one of the main reasons why this stream still has got takers in India! This stream is made up of parts like humanities, visual arts, performing arts, literary arts etc. In this article, you will be reading details about arts stream schooling (11th and 12th) such as- education boards available in India, important subjects to study.',
      click: '/ArtsQue',
    },
    {
      title: 'Commerce',
      descript: 'Commerce is one of the most popular options that many students pick. This is a study related to finance, trade, economy, and business activities. If you have a lot of interest in numbers, trading, and analyzing data, commerce is the best choice. Along with all this, you will also be able to enjoy a better career option. This is also a good choice for those who don’t want to go with science and mathematics subjects. That is because mathematics is an optional subject.',
      click: '/ComQue',
    },
    {
      title: 'Science',
      descript: 'A majority of students are interested in taking the science stream. Before you opt for the stream, You should note that the Science Stream is further divided into two parts. These two parts are Medical & Non-Medical. There is a minor variation in the subjects since some of the subjects remains common. Now you must have a lot of questions before choosing Science in Class 11 & Class 12. Go ahead and check out best suit for you.',
      click: '/SciQue',
    }
  ]

  return (
    <div className="App">
      <Header />

      <section className="page-section stream-sec" id="portfolio">
        <div className="stream">
          <div className="row">
            <div className="col-lg-12 text-center">
              <h2 className="section-heading text-uppercase">Streams</h2>
              <h3 className="section-subheading text-muted">Streams to choose from.</h3>
            </div>
          </div>
          <div className="row port-row">
            {
                fields && fields.map(Fields =>
                    <div className="col-md-4 col-sm-6 portfolio-item">
                      <div className='portfo'>
                        <div className="portfolio-work">
                            <h4 className='port-ttll'>{Fields.title}</h4>
                            <p className="mtd">{Fields.descript}</p>
                        </div>
                        <div className='text-cntr'>
                            <div>
                              <Button className='my-4' color='primary' type='button' onClick={() => {props.history.push(Fields.click)}} >
                                Select
                              </Button>
                            </div>
                        </div>
                      </div>
                    </div>
                )
            }
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
}

export default Twelve;
