import React, { useLayoutEffect, useState, useCallback } from 'react';
import { Button } from 'reactstrap';

import axios from 'axios';

import Header from '../../components/Header';
import Footer from '../../components/Footer';

const token = 'YWRtaW5hZG1pbjEyMw==';

function Diploma(props) {
  useLayoutEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
      if(Log != true){
        props.history.push("/");
      }
    }, []);

  const fields = [
    {
      title: 'Computer',
      descript: 'Computer engineering is a branch of engineering that integrates several fields of computer science and electronic engineering required to develop computer hardware and software. Computer engineers usually have training in electronic engineering, software design, and hardware-software integration. This field of engineering not only focuses on how computer systems themselves work but also how they integrate into the larger picture.',
      click: '/CompQue'
    },
    {
      title: 'Information Technology',
      descript: 'The purpose of the Diploma in Information Technology is to provide students with a wide spectrum of study covering the breadth of Information Technology. Upon completion, students will have the ability to apply the concepts and technologies into practice. IT include Software Development, Computer System Analysis, Computer Programming Database Information System Support, Web Development and Project Management.',
      click: '/ITQue'
    },
    {
      title: 'Electronics',
      descript: 'Electronic engineering (also called electronics and communications engineering) is an electrical/electronics engineering discipline which utilizes nonlinear and active electrical components (such as semiconductor devices, especially transistors and diodes) to design electronic circuits, devices, integrated circuits and their systems. The discipline typically also designs passive electrical components, usually based on printed circuit boards.',
      click: '/EleQue'
    }
  ]


  return (
    <div className="App">
      <Header />

      <section className="page-section stream-sec" id="portfolio">
        <div className="stream">
          <div className="row">
            <div className="col-lg-12 text-center">
              <h2 className="section-heading text-uppercase">Streams</h2>
              <h3 className="section-subheading text-muted">Streams to choose from.</h3>
            </div>
          </div>
          <div className="row port-row">
            {
                fields && fields.map(Fields =>
                    <div className="col-md-4 col-sm-6 portfolio-item">
                      <div className='portfo'>
                        <div className="portfolio-work">
                            <h4 className='port-ttll'>{Fields.title}</h4>
                            <p className="mtd">{Fields.descript}</p>
                        </div>
                        <div className="text-cntr">
                            <div>
                              <Button className='my-4' color='primary' type='button' onClick={() => {props.history.push(Fields.click)}} >
                                Select
                              </Button>
                            </div>
                        </div>
                      </div>
                    </div>
                )
            }
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
}

export default Diploma;
