import React, { useState, useLayoutEffect } from 'react';
import axios from 'axios';

import history from '../history.js';

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
  Media,
  Label
} from 'reactstrap';

const EduDetails = (props) => {

const [values, setValues] = useState({
    u_id: global.foo, country: 'India', state: 'Maharashtra', board: 'Pune', medium: 'English', standard: '10 TH',
  });

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },
    };

  const SignIn = () => {
    axios.post(
      'https://globalarey.in/career_guidance/api/education/',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);

      if(response.data.response.message == 'new education added.') {
        console.log('true');
        props.history.push("/");
      }
      else {
        alert(response.data.response.message)
        console.log('false');
      }

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
  }

  return(
    <div>
    <section className="ftco-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-7">
              <div className="wrap">
                <div className="img-edu">
                  <img className="img-fluid-edu" src={ `https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Ftse2.mm.bing.net%2Fth%3Fid%3DOIP.iDnBQNVrdYhG1LSPqHtNxgHaDf%26pid%3DApi&f=1` } alt="portfolio_img" />
                </div>
                <div className="login-wrap p-4 p-md-5 mg-btm">
                  <div className="d-flex">
                    <div className="w-100">
                      <h3 className="mb-4">Educational Details</h3>
                    </div>
                  </div>
                  <Form role='form' className="signin-form">

                <Row>
                  <Col>
                    <FormGroup className="form-group edu-reg mt-3">
                  <Label for="exampleEmail">Highest Education</Label>
                  <Input className="form-control" placeholder='Username' type='select' value={values.standard} onChange={set('standard')}>
                    <option value="10 TH">10th</option>
                    <option value="12 TH">12th</option>
                    <option value="Diploma">Diploma</option>
                  </Input>
                </FormGroup>
                </Col>
                  <Col>
                    <FormGroup className="form-group edu-reg mt-3">
                  <Label for="exampleEmail">Board</Label>
                  <Input className="form-control" placeholder='Username' type='select' value={values.board} onChange={set('board')}>
                    <option value="Pune">Pune</option>
                    <option value="Kokan">Kokan</option>
                    <option value="Mumbai">Mumbai</option>
                  </Input>
                </FormGroup>
                </Col>
                <Col>
                <FormGroup className="form-group edu-reg mt-3">
                  <Label for="exampleEmail">Medium</Label>
                  <Input className="form-control" placeholder='Username' type='select' value={values.medium} onChange={set('medium')}>
                    <option value="English">English</option>
                    <option value="Marathi">Marathi</option>
                    <option value="Semi English">Semi English</option>
                  </Input>
                </FormGroup>
                </Col>
                </Row>
                
                <Row>
                  <Col>
                <FormGroup className="form-group edu-reg mt-3">
                  <Label for="exampleEmail">Country</Label>
                  <Input className="form-control" placeholder='Username' type='select' value={values.country} onChange={set('country')}>
                    <option value="India">India</option>
                    <option value="US">US</option>
                    <option value="China">China</option>
                  </Input>
                </FormGroup>
                </Col>
                  <Col>
                    <FormGroup className="form-group mt-3">
                  <Label for="exampleEmail">State</Label>
                  <Input className="form-control" placeholder='Username' type='select' value={values.state_id} onChange={set('state')}>
                    <option value="Maharashtra">Maharashtra</option>
                    <option value="JK">JK</option>
                    <option value="UP">UP</option>
                  </Input>
                </FormGroup>
                </Col>
                </Row>
                <div className='text-center'>
                  <Button className='my-4' color='primary' type='button' onClick={SignIn} >
                    Sign In
                  </Button>
                </div>
              </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default EduDetails;
