import React, { useState, useLayoutEffect } from 'react';
import axios from 'axios';

import Header from '../components/Header';
import Footer from '../components/Footer';

import history from '../history.js';

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
  Media,
  Label
} from 'reactstrap';

const NewBlog = (props) => {

const [values, setValues] = useState({
    author_name: '', compnay_name: '', designation: '', blog_description: '',
  });

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },
    };

    useLayoutEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
        if(Log != true){
          props.history.push("/");
        }
    }, []);

  const Create = () => {
    axios.post(
      'https://globalarey.in/career_guidance/api/blogs/',
      values,
      config,
    ).then((response)=> {
      console.log(response);

      if(response.data.response.message == 'new Blogs added.') {
        alert(response.data.response.message);
        props.history.push("/Main");
      }
      else {
        alert(response.data.response.message)
        console.log('false');
      }
    }).catch();
    console.log(values);
  }

  return(
    <div>
      <Header />

    <div className='crt-blog'>
    <section className="ftco-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-7">
              <div className="wrap">
                <div className="login-wrap p-4 p-md-5 mg-btm">
                  <div className="d-flex">
                    <div className="w-100">
                      <h3 className="mb-4">Add New Blog</h3>
                    </div>
                  </div>
                  <Form role='form' className="signin-form">
                    <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>

                    <Input className="form-control" placeholder='Name' type='text' autoComplete='new-email' onChange={set('author_name')}/>
                  </InputGroup>
                </FormGroup>

                <Row>
                  <Col>
                    <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>

                    <Input className="form-control" placeholder='Company' type='text' autoComplete='new-email' onChange={set('compnay_name')}/>
                  </InputGroup>
                </FormGroup>
                </Col>
                <Col>
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>

                    <Input className="form-control" placeholder='Designation' type='text' autoComplete='new-email' onChange={set('designation')}/>
                  </InputGroup>
                </FormGroup>
                </Col>
                </Row>
                
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>

                    <Input className="form-control" placeholder='Description' type='textarea' rows='5' autoComplete='new-email' onChange={set('blog_description')}/>
                  </InputGroup>
                </FormGroup>

                <div className='text-center'>
                  <Button className='my-4' color='primary' type='button' onClick={Create} >
                    Add Blog
                  </Button>
                </div>
              </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      </div>

      <Footer />
    </div>
  )
}

export default NewBlog;
