import React, { useState, useLayoutEffect, useEffect } from 'react';
import axios from 'axios';
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  CardText,
  Progress,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from 'reactstrap';

import history from '../history.js';
import Header from '../components/Header';
import Footer from '../components/Footer';

const token = 'YWRtaW5hZG1pbjEyMw==';

const Profile = (props) => {
  // const [data, setData] = useState([])
	const [color, setColor] = useState('danger');
	const [port, setPort] = useState([]);
	const [exCount, setExCount] = useState(0);
  const [scores, setScores] = useState([])
  const [index, setIndex] = useState(0);
  const [nextPort, setNextPort] = useState([]);
  const [max, setMax] = useState(0);
  const [service, setService] = useState('');
  const [stream, setStream] = useState('');
  const [viewSt, setViewSt] = useState(false)

  const {
    buttonLabel,
    className
  } = props;

  const [modal, setModal] = useState(false);

  const toggle = () => {
    setModal(!modal);
    setViewSt(!viewSt);
  }

let User = sessionStorage.getItem('user')
      User = JSON.parse(User)
      let Std = sessionStorage.getItem('standard');   
      Std = JSON.parse(Std)

  useLayoutEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
      if(Log != true){
        props.history.push("/");
      }
    }, []);

  useEffect(() => {
    getData();
  }, []);

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

const Max = () => {
  MaxT();
  setViewSt(!viewSt)
}

const MaxT = () => {
  for (var i = 0; i < exCount; i++) {
    setNextPort(port[i]);
    if(nextPort.rightanswer > max) {
    setMax(nextPort.rightanswer);
  } else {
    console.log(max)
  }
  }
  SetPriceType(max)
}

const ShowRslt = () => {
  MaxT();
  toggle();
}

useEffect(() => {
    MaxT(); 
}, [nextPort])

  const data = {
     "u_id": User.u_id
  };

  const getData = () =>{
  axios.post(
    'https://globalarey.in/career_guidance/api/studentexams/single_read.php',
    data,
    config,
  ).then((response)=> {
  	setExCount(response.data.response.examCount)
    setPort(response.data.response.studentExam)
  }).catch();
}

const SetPriceType = (itemValue, itemIndex) => {
        setService(itemValue);
        setStream(findPrice(port, itemValue));
      }

const findPrice = (port, max) => {
const el = port.find(el => el.rightanswer === max); // Possibly returns `undefined`
return el && el.stream; // so check result is truthy and extract `id`
}

const url = 'https://globalarey.in/career_guidance/api/user/email.php?name=' + User.name + '&email=' + User.email + '&standard=' + Std + '&stream=' + stream + '&score=' + max;

const sendMail = () => {
  axios.get(
    url,
    config,
  ).then((response)=>{
  
  }).catch();
  alert("Email has been sent to admin...!")
  setModal(!modal)
}
	return (
		<div>

			<Header />
		
			<div className='profile'>
				<div className='prof'>
			      <div className='prof-crd'><div className='prof-text'>Name:&nbsp; </div>{User.name}</div>
			      <div className='prof-crd'><div className='prof-text'>Email:&nbsp; </div>{User.email}</div>
			      <div className='prof-crd'><div className='prof-text'>Phone:&nbsp; </div>{User.phone}</div>
			    </div>
			    <div className='prof'>
			      <div className='prof-crd'><div className='prof-text'>Total Tests:&nbsp; </div>{exCount}</div>
            <div className='prof-crd'><div className='prof-text'>Maximum Score:&nbsp; </div>{max}</div>
            <div className='prof-crd'><div className='prof-text'>Stream:&nbsp; </div>{stream}</div>
			    </div>
			</div>

			<div className="test-cont">
        <div className='result'>
				  <div className='test-rst'>Test Results</div>
          <div>
            <Button color='warning' type='button' onClick={ShowRslt} >
              Evaluate
            </Button>
          </div>
        </div>

				<div className='row'>
	      {
					port && port.map(Port =>
						<div className="col-md-3 col-sm-4 test">
							<div className="text-center">{Port.stream}</div>
              <Progress color={color} value={Port.rightanswer} max={5}>{Port.rightanswer}</Progress>
           	</div>
          )
        }
        </div>
    </div>   

    <div>
    <Modal isOpen={modal} toggle={toggle}>
      <ModalBody className='mod'>
        <div className='test-rst'>Highest Scored Stream</div>
        <div className='test-score'>Score: {max}</div>
        { viewSt ? (
          <div>
              <Button color='success' type='button' onClick={Max} >
                Show Stream
              </Button>
            </div>
          ) : (
            <div>
              <div className='test-stream'>{stream}</div>
              <div>
                <Button color='success' type='button' onClick={sendMail} >
                  Request Guide
                </Button>
              </div>
            </div>
          )
        }
       
      </ModalBody>
      <ModalFooter>
        <Button color="success" onClick={toggle}>Okay</Button>
      </ModalFooter>
    </Modal>
  </div> 

			<Footer />

		</div>
	)
}

export default Profile;