import React, { useState, useLayoutEffect } from 'react';
import axios from 'axios';
import history from '../history.js';

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
  Media,
} from 'reactstrap';

const Login = (props) => {

  const [values, setValues] = useState({
    email: '', password: '',
  });

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },
    };

    useLayoutEffect(() => {
      sessionStorage.clear();
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
        if(Log != true){
          props.history.push("/");
        }
    }, []);

  const Abc = () => {
    const reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

      if(reg.test(values.email) === false)
        {
          alert('Enter email address properly');
        }
        else if(values.email === '')
        {
          alert('Email field is empty..');
        }
        else
        {
          if(values.password.length <7)
            {
              alert('enter password properly');
            }
            else if(values.password ==='')
            {
              alert('password field is empty..');
            }
            else
            {
              if(values.password.length >=7 && reg.test(values.email) === true)
              {
    axios.post(
      'https://globalarey.in/career_guidance/api/user/login.php',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response.data.response.flag);

      sessionStorage.setItem('lflag', JSON.stringify(response.data.response.flag));
      sessionStorage.setItem('user', JSON.stringify(response.data.response.user));
      sessionStorage.setItem('log', JSON.stringify(true));
      sessionStorage.setItem('standard', JSON.stringify(response.data.response.standard));
      let Std = sessionStorage.getItem('standard')
      Std = JSON.parse(Std)
      console.log(Std)
      let lFlag = sessionStorage.getItem('lflag')
      lFlag = JSON.parse(lFlag)

      if(response.data.response.flag == true) {
        console.log('true');
        props.history.push("/Main");
      }
      else {
        console.log('false');
      }

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
    }
     }
  }
  }

  const Register = () => {
    props.history.push('/Register')
  }

    return (
      <>
        <section className="ftco-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-7 col-lg-5">
              <div className="wrap">
                <div className="img">
                  <img className="img-fluid" src={ `https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Ftse2.mm.bing.net%2Fth%3Fid%3DOIP.iDnBQNVrdYhG1LSPqHtNxgHaDf%26pid%3DApi&f=1` } alt="portfolio_img" />
                </div>
                <div className="login-wrap p-4 p-md-5 mg-btm">
                  <div className="d-flex">
                    <div className="w-100">
                      <h3 className="mb-4">Sign Up</h3>
                    </div>
                  </div>

              <Form role='form' className="signin-form">
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>
                    <Input className="form-control" placeholder='Email' type='email' autoComplete='new-email' value={values.email} onChange={set('email')} required/>
                  </InputGroup>
                </FormGroup>
                <FormGroup className="form-group mt-3">
                  <InputGroup className='input-group-alternative'>
                    <Input className="form-control" placeholder='Password' type='password' autoComplete='new-password' value={values.password} onChange={set('password')} required/>
                  </InputGroup>
                </FormGroup>
                <div className='custom-control custom-control-alternative custom-checkbox'>
                  <input
                    className='custom-control-input'
                    id=' customCheckLogin'
                    type='checkbox'
                  />
                  <label
                    className='custom-control-label'
                    htmlFor=' customCheckLogin'
                  >
                    <span className='text-muted'>Remember me</span>
                  </label>
                </div>
                <div className='text-center'>
                  <Button className='my-4' color='primary' type='button' onClick={Abc} >
                    Sign in
                  </Button>
                </div>
                <div className='nUser'>
                  <span className='text-muted'>New User?</span>
                  <Button color='primary' type='button' onClick={Register} >
                    Sign Up
                  </Button>
                </div>
              </Form>
            </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      </>
    );
  }

export default Login;
